$main-color: #fda50f;
.about.container {
  padding-top: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

.my-info {
  position: absolute;
  top: 75px;
  left: 25px;
  opacity: 0;
  .toggleSlide {
    display: none;
  }
  .q-list {
    display: flex;
    flex-direction: column;
    gap: 30px;
    position: relative;

    &::after {
      content: "";
      position: absolute;
      top: -10px;
      right: 0px;
      width: 50px;
      height: 50px;
      border-right: 2px solid $main-color;
      border-top: 2px solid $main-color;
      border-top-right-radius: 5px;
    }
    &::before {
      content: "";
      position: absolute;
      bottom: -20px;
      right: 0px;
      width: 50px;
      height: 50px;
      border-right: 2px solid $main-color;
      border-bottom: 2px solid $main-color;
      border-bottom-right-radius: 5px;
    }
    .q-box {
      max-width: 300px;
      text-align: start;
      .question {
        font-size: 18px;
        color: #fda50f;
        margin-bottom: 5px;
        letter-spacing: 3px;
      }
      .answer {
        font-size: 18px;
        font-family: "Alegre Sans";
        color: #8690ae;
      }
    }
  }
}
.q-section {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin-top: 50px;
  transition-duration: 0.8s;
  .q {
    padding-bottom: 30px;
    text-align: start;
    opacity: 0;
    // &:first-child {
    //   animation: fadeInUp 1s 1.3s forwards;
    // }
    // &:nth-child(2) {
    //   animation: fadeInUp 1s 1.6s forwards;
    // }
    // &:last-child {
    //   animation: fadeInUp 1s 1.9s forwards;
    //   border: none;
    // }
  }
  .bQuestion {
    font-size: 22px;
    color: $main-color;
    margin-bottom: 10px;
    text-align: start;
  }
  .bAnswer {
    max-width: 800px;
    font-size: 20px;
    letter-spacing: 1px;
    font-family: "Alegre Sans";
    color: #8690ae;
    a {
      color: $main-color;
      position: relative;
      &::after {
        content: "";
        background-color: $main-color;
        position: absolute;
        width: 0;
        height: 1px;
        bottom: -2px;
        left: 0;
        transition: 0.2s ease-out;
      }
      &:hover::after {
        width: 100%;
      }
    }
  }
}

@media (max-width: 767px) {
  .my-info {
    .toggleSlide {
      color: $main-color;
      display: block;
      text-align: center;
      writing-mode: vertical-lr;
      position: absolute;
      right: -25px;
      top: 50%;
      transform: translateY(-50%);
      .toggleBtn {
        transition: 0.4s;
      }
    }
    position: fixed;
    top: 50vh;
    left: -285px;
    padding: 20px;
    opacity: 0;
    animation: fadeIn 1s 2s forwards;
    transition-duration: 1s;
    z-index: 2;
    .q-list {
      display: flex;
      flex-direction: column;
      gap: 20px;
      position: relative;

      &::after {
        content: "";
        position: absolute;
        top: -10px;
        right: 0px;
        width: 20px;
        height: 20px;
        border-right: 2px solid $main-color;
        border-top: 2px solid $main-color;
        border-top-right-radius: 5px;
      }
      &::before {
        content: "";
        position: absolute;
        bottom: -20px;
        right: 0px;
        width: 20px;
        height: 20px;
        border-right: 2px solid $main-color;
        border-bottom: 2px solid $main-color;
        border-bottom-right-radius: 5px;
      }
      .q-box {
        max-width: 300px;
        text-align: start;
        .question {
          font-size: 1rem;
          color: #fda50f;
          margin-bottom: 5px;
          letter-spacing: 3px;
        }
        .answer {
          font-size: 0.8rem;
          font-family: "Alegre Sans";
          color: #8690ae;
        }
      }
    }
  }
}
